.slider {
    .figure {
        img {
            display: block;
            margin: 0 auto; } } }
.owl-pagination {
    position: relative;
    margin-top: -30px;
    margin-left: 0;
    margin-right: 0;
    text-align: center; }
.owl-buttons {
    position: relative;
    color: white;
    font-size: 50px; }
.owl-prev, .owl-next {
    position: absolute;
    opacity: 0.5;
    transition: opacity 1s cubic-bezier(.19,1,.22,1); }
.owl-prev {
    left: 15px;
    bottom: 150px; }
.owl-next {
    right: 15px;
    bottom: 150px; }
.owl-prev:hover, .owl-next:hover {
    opacity: 1; }
.owl-item {}

